.program-selection {
  width: 320px;
  margin: 0 auto;
  background-color: @bg-white;
}

.program-selection__main-title {
  margin: 0;
  margin-top: 15px;
  margin-left: 20px;
  margin-bottom: 26px;
  padding: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 60px;
  color: @bg-almostblack;
}

.program-selection__description {
  width: 226px;
  margin: 0;
  margin-left: 20px;
  margin-bottom: 22px;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: @bg-almostblack;
  text-transform: uppercase;
}

.program-selection__wrapper {
  padding: 0;
  border: none;
}

.program-selection__box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-self: stretch;
  min-height: 167px;
  padding-top: 18px;
  padding-bottom: 17px;
}

.program-selection__box p {
  margin: 0;
}

.program-selection__label {
  margin-left: 18px;
  margin-right: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: @bg-blackbrown;
  text-transform: uppercase;
}

input[type="email"],
input[type="tel"],
input[type="number"],
input[type="text"] {
  height: 42px;
  padding-left: 13px;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", "Arial", "Verdana", "Helvetica", sans-serif;
  color: @bg-bluishblack;
  text-transform: uppercase;
  vertical-align: center;
  border: 1px solid rgba(205, 205, 205, 0.5);
  box-sizing: border-box;

  &:hover {
    border: 1px solid @bg-darkgrey;
  }

  &:focus,
  &:active {
    border: 2px solid @bg-bluishblack;
    outline: none;
  }
}

.program-selection__case .program-selection__error {
  border: 2px solid @bg-errorform;
}

::-webkit-input-placeholder {
  color: @bg-bluishblack;
}

::-moz-placeholder {
  color: @bg-bluishblack;
}

:-moz-placeholder {
  color: @bg-bluishblack;
}

:-ms-input-placeholder {
  color: @bg-bluishblack;
}

input[type="text"] {
  color: #000000;
}

input[name="login"] {
  max-width: 215px;
}

input[name="weight"] {
  max-width: 192px;
}

input[name="age"] {
  max-width: 159px;
  margin-bottom: 9px;
}

input[name="email"] {
  max-width: 218px;
}

input[name="phone"] {
  max-width: 200px;
}

.program-selection__user {
  width: 280px;
  height: 150px;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  font-family: "Oswald", "Arial", "Verdana", "Helvetica", sans-serif;
  color: @bg-bluishblack;
  text-transform: uppercase;
  border: 1px solid rgba(205, 205, 205, 0.5);
}

.program-selection__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 253px;
  min-height: 106px;
  .list-reset();
}

.program-selection__border {
  height: 106px;
  padding-top: 25px;
  padding-bottom: 25px;
  border: 1px solid rgba(205, 205, 205, 0.5);
}

.program-selection__radio-wrap {
  margin-bottom: 28px;
}

.radio__item .program-selection__label {
  position: relative;
  display: block;
  margin: 0;
  margin-left: 63px;
  cursor: pointer;
  user-select: none;
}

input[type="radio"] + .program-selection__label::before {
  content: "";
  position: absolute;
  left: -46px;
  top: -6px;
  width: 30px;
  height: 30px;
  border: 1px solid @bg-darkgrey;
  border-radius: 50%;
  box-sizing: border-box;
}

input[type="radio"]:checked + .program-selection__label::after {
  content: "";
  position: absolute;
  left: -37px;
  top: 3px;
  width: 12px;
  height: 12px;
  background-color: @bg-ligthgreen;
  border-radius: 50%;
}

input[type="radio"] + .program-selection__label:hover::before,
input[type="radio"]:focus + .program-selection__label::before {
  border-color: @bg-darklygrey;
}

input[type="radio"]:disabled + .program-selection__label {
  opacity: 0.3;
}

.program-selection__contact {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-self: stretch;
  min-height: 100px;
  padding-top: 15px;
  padding-bottom: 20px;
}

.program-selection__case p {
  margin: 0;
}

.program-selection__legend {
  position: relative;
  width: 185px;
  margin: 0;
  margin-left: 18px;
  padding: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: @bg-almostblack;

  &::before {
    content: "";
    position: absolute;
    height: 3px;
    background-color: @bg-ligthgreen;
  }

  &::after {
    content: "";
    position: absolute;
    height: 3px;
    background-color: @bg-ligthgreen;
  }
}

.program-selection__email {
  position: absolute;
  top: 31px;
  left: 265px;
}

input[type="email"]:focus + .program-selection__email,
input[type="email"]:active + .program-selection__email {
  fill: @bg-ligthgreen;
}

.program-selection__error + .program-selection__email {
  fill: @bg-errorform;
}

.program-selection__phone {
  position: absolute;
  top: 87px;
  left: 265px;
}

input[type="tel"]:focus + .program-selection__phone,
input[type="tel"]:active + .program-selection__phone {
  fill: @bg-ligthgreen;
}

.program-selection__error + .program-selection__phone {
  fill: @bg-errorform;
}

.legend-contact::after {
  top: 15px;
  right: -370px;
  width: 350px;
}

.legend-contact::before {
  top: 15px;
  left: -380px;
  width: 350px;
}

.legend-contact {
  margin-bottom: 20px;
}

.email__wrapper {
  position: relative;
  margin-bottom: 12px;
}

.legend-comment::after {
  top: 19px;
  right: -410px;
  width: 390px;
}

.legend-comment::before {
  top: 19px;
  left: -380px;
  width: 350px;
}

.program-selection__comment textarea {
  margin-top: 34px;
  margin-left: 19px;
  margin-bottom: 23px;
  padding-top: 11px;
  padding-left: 15px;
  box-sizing: border-box;

  &:hover {
    border: 1px solid @bg-darkgrey;
  }

  &:focus,
  &:active {
    border: 2px solid @bg-bluishblack;
    outline: none;
  }
}

.legend-optionally {
  margin-bottom: 39px;
}

.legend-optionally::after {
  top: 14px;
  right: -340px;
  width: 360px;
}

.legend-optionally::before {
  top: 14px;
  left: -380px;
  width: 350px;
}

.optionally-wrapper {
  margin-bottom: 45px;
}

.program-selection__optionally {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 140px;
  min-height: 142px;
  .list-reset();
}

.checkbox__item .program-selection__label {
  position: relative;
  display: block;
  height: 14px;
  margin-left: 63px;
  cursor: pointer;
  user-select: none;
}

input[type="checkbox"] + .program-selection__label::before {
  content: "";
  position: absolute;
  left: -46px;
  top: -4px;
  width: 28px;
  height: 28px;
  border: 1px solid @bg-darkgrey;
}

input[type="checkbox"]:checked + .program-selection__label::after {
  content: "";
  position: absolute;
  left: -40px;
  top: 3px;
  width: 21px;
  height: 16px;
  background-image: url("../img/tick.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 21px 16px;
}

input[type="checkbox"] + .program-selection__label:hover::before,
input[type="checkbox"]:focus + .program-selection__label::before {
  border-color: @bg-darklygrey;
}

input[type="checkbox"]:disabled + .program-selection__label {
  opacity: 0.3;
}

.program-selection__button {
  margin-left: 19px;
  margin-bottom: 20px;
  padding: 11px 78px;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

.program-selection__necessary {
  margin-left: 84px;
  margin-bottom: 32px;
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
  font-family: "Arial MT", "Arial", "Verdana", "Helvetica", sans-serif;
  color: @bg-bluishblack;
}

